@import url(https://fonts.googleapis.com/css2?family=Libre+Baskerville:ital@0;1&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}

.app{height:1800vh;position:relative;background:#e0e0e0;display:flex;justify-content:center;align-items:center}

.page{position:fixed;top:0px;width:100%;height:100%;box-shadow:0px 0px 10px rgba(0,0,0,0.25),0px 0px 10px rgba(0,0,0,0.25),0px 0px 10px rgba(0,0,0,0.25),0px 0px 10px rgba(0,0,0,0.25);border-bottom-right-radius:25px;border-bottom-left-radius:25px;-webkit-transform-origin:center center;transform-origin:center center}.page.home{background:#f1efec;z-index:4;display:flex;justify-content:center;align-items:center}

.intro{display:flex;justify-content:center}.scroll-indicator{position:fixed;bottom:21px;text-transform:lowercase;font-family:"Libre Baskerville", serif;font-size:9px;font-style:italic;font-weight:bold;letter-spacing:1.5px}.scroll-indicator span{display:inline-block}.scroll-border{position:absolute;bottom:13px;left:0;width:100%;height:2px;background:black}.intro-grid{width:calc(40px * 11);height:calc(40px * 8);display:flex;justify-content:center;align-items:center;flex-wrap:wrap;position:relative;overflow:hidden}.intro-background{position:absolute;top:0;left:0;width:100%;height:100%;background:#f1efec;mix-blend-mode:difference;border-bottom-right-radius:25px;border-bottom-left-radius:25px}.intro-item{width:40px;height:40px;position:relative}.line{width:3px;height:50%;left:50%;display:block;position:absolute;background:#0e1013}.line:first-child{top:0;-webkit-transform-origin:bottom;transform-origin:bottom;-webkit-transform:rotate(90deg);transform:rotate(90deg)}.line:last-child{top:50%;-webkit-transform-origin:top;transform-origin:top}

